import React from "react";
import styled from "styled-components";

const Footer = () => {
  return (
    <FooterContainer>
      <Title>Visa Expiry Issue</Title>
      <Text>
        &copy; 2023 - {new Date().getFullYear()} Visa Expiry Issue. Design By{" "}
        <Develop href="#">Jahangir Alam</Develop>
      </Text>
    </FooterContainer>
  );
};

export default Footer;

export const FooterContainer = styled.footer({
  width: "100%",
  height: "70px",
  background: "rgba(19, 15, 64,1.0)",
  display: "grid",
  gridTemplateColumns: "auto",
  padding: "1rem 0.5rem 0.3rem 0.5rem",
});

export const Title = styled.h1({
  fontWeight: "500",
  fontSize: "1.2rem",
  textAlign: "center",
  color: "#fff",
});

export const Text = styled.p({
  fontSize: ".8rem",
  color: "#fff",
  textAlign: "center",
});

export const Develop = styled.a({
  fontSize: ".8rem",
  color: "rgba(240, 147, 43,1.0)",
  fontWeight: "600",
});
